.login-input {
    width: 100%;
    padding: 14px 0 14px 14px;
    font-size: 21px;
}

.login-input {
    margin-top: 30px;
}

.login-input:first-child {
    margin-top: 0;
}

.login-button {
    width: 100%;
    padding: 14px 0 14px 14px;
    font-size: 21px;
}

.login-button {
    margin-top: 30px;
    background-color: white;
    color: black;
    cursor: pointer;
}

.login-button:hover {
    background-color: black;
    color: white;
}

.login-button:first-child {
    margin-top: 74px;
    background-color: black;
    color: white;
}

.login-button:first-child:hover {
    background-color: white;
    color: black;
    border: 2px solid;
}

@media (max-width:768px) {
    .login-input {
        height: 40px;
        font-size: 16px;
        margin-top: 9px;
    }

    .login-button {
        padding: 14px 14px 14px 14px;
        font-size: 14px;
        border: 2px solid;
        background-color: white;
        color: black;
    }

    .login-button:first-child {
        margin-top: 60px;
    }
}