.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  border-radius: 0;
}

.modal-content {
  background: white;
  padding: 40px;
  border-radius: 8px;
  position: relative;
  width: 855px;
  height: 710px;
  border-radius: 0;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

@media (max-width:768px) {
  .modal-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    width: 90%;
    height: auto;
  }
}