.login-input {
    width: 100%;
    padding: 14px 0 14px 14px;
    font-size: 21px;
}

.login-input {
    margin-top: 30px;
}

.login-input:first-child {
    margin-top: 0;
}

.login-button {
    width: 100%;
    padding: 14px 0 14px 14px;
    font-size: 21px;
}

.login-button {
    margin-top: 30px;
    background-color: white;
    color: black;
    cursor: pointer;
}

.login-button:first-child {
    margin-top: 74px;
    background-color: black;
    color: white;
}

@media (max-width:768px) {
    .login-button:first-child{
        margin-top: 60px;
    }

    .login-button{
        margin-top: 9px;
    }
}